.AuthScreen {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60vh;

}

.note-maker-name {
    color: rgb(90, 90, 90);
    font-size: 10.3px;
}

.HomeContent {
    margin-top: .2cm;

    h1 {
        font-weight: 500;
    }

    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
    height: 100%;
    overflow-y: auto;

}

.write-box {
    display: flex;
    flex-direction: column;
    row-gap: .3cm;

}

.home-img {
    width: 1cm;
    height: 1cm;
    cursor: pointer;
}

.note-img {
    width: 1.8cm;
    height: 1.8cm;
    border-radius: 100px;

    border: 1px solid rgb(194, 194, 194);
}

.note-decision {
    position: fixed;

    z-index: 1;
    overflow-y: hidden;
    display: none;

    overflow-y: scroll;
    min-width: 90vw;
    min-height: 80vh;

    justify-content: center;
    align-items: flex-start;
    backdrop-filter: blur(3px);

}

.note-decision-box {
    border: 1px solid rgb(210, 210, 210);
    background-color: rgb(237, 237, 237);
    padding: .5cm;
    display: flex;
    flex-direction: column;
    margin-top: 1cm;
    row-gap: .5cm;
    align-items: flex-end;

}

.close-note {
    width: .32cm;
    height: .32cm;
    cursor: pointer;
}

.note-box-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: .3cm;
    flex-wrap: wrap;
    row-gap: .3cm;
}

.leave-note {
    padding: .2cm .5cm;
    background-color: rgb(81, 122, 247);
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.delete-note {
    padding: .2cm .5cm;
    background-color: rgb(246, 79, 67);
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.note {
    width: fit-content;

    margin-right: 0.4cm;
    display: flex;
    flex-direction: column;
    cursor: pointer;

}

.note-content {
    font-size: 12px;
    padding: 2px 5px;
    background-color: #f9f9f9;
    border-radius: 5px;
    border: 1px solid #ccc;
    max-width: 2cm;
    margin-left: 1.2cm;
    margin-top: -.6cm;
    word-break: break-all;
}








.plus-note {
    width: .6cm;
    height: .6cm;
    margin-left: 1.2cm;
    margin-top: -.5cm;
}

.home-upper {
    display: flex;
    align-items: flex-start;


    justify-content: flex-start;

    padding-bottom: .2cm;
    overflow: scroll;
    width: 100%;


    border-bottom: 1px solid rgb(199, 199, 199);
}

.home-feed {
    box-shadow: 0px 2px 0px #000000;
    fill: black;

}

.feeds-head {
    display: flex;
    justify-content: center;
    column-gap: .1cm;
    cursor: pointer;
    align-items: center;

    h2 {
        font-weight: 500;
    }
}

.main-home {
    margin: 0cm 0cm .2cm 0cm;
}

.add-link-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#incorrectLink {
    display: none;
}

.link-box {
    display: flex;
    column-gap: 3cm;
    align-items: center;
    border: 1px solid rgb(195, 195, 195);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: .2cm .5cm;
    border-radius: 5px;
    background-color: rgb(234, 234, 234);
}

.links {
    display: flex;
    flex-direction: column;
    width: fit-content;
    align-items: flex-start;
    row-gap: .2cm;
    justify-content: center;

}

.links-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logout-icon {
    width: .8cm;
    height: .8cm;
    cursor: pointer;
}

.linkImg {
    width: .6cm;
    height: .6cm;
}

.link {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 0.1cm;
    width: fit-content;
}

.logout-div {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.write {
    width: 10cm;
    height: 2cm;
    border: thin solid rgb(197, 197, 197);
    padding: .3cm .5cm;
    background-color: whitesmoke;
    font-family: roboto;
}

.post {
    background-color: royalblue;
    border: none;
    padding: .2cm .5cm;
    color: white;
    border-radius: 3px;
    cursor: pointer;
}

.icon {
    width: 1.6cm;
    pointer-events: none;
}

.h-users {
    display: flex;
    column-gap: 1cm;
    align-items: center;
    padding-bottom: 1cm;
}

.AuthBox {
    width: 10cm;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: .5cm;
    padding: 1cm 1cm;

}

.welcome {
    font-weight: 500;
}

.welcome-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: .1cm;
}

.home-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: .4cm;
}

.home-button {
    font-size: 14px;
    padding: .3cm .5cm;
    border-radius: 5px;
    border: none;
    color: black;
    background-color: rgb(100, 236, 159);
    cursor: pointer;
}