.SignUp {
    display: flex;
    flex-direction: column-reverse;
    row-gap: .5cm;
    justify-content: center;
    align-items: center;
    min-height: 70vh;
}

p {
    margin: 0px 0px;
}

.signup-message {
    font-size: 13px;
    color: rgb(71, 71, 71);
}

.signup-signin {
    font-size: 14px;
    color: rgb(31, 31, 31);
}
#emailExist{
    display: none;
}
#invalidPass{
    display: none;
}
#invalidEmail{
    display: none;
}
.disabled-signup-button{
    opacity: .5;
}
.sub-search {
    color: rgb(101, 101, 101);
    font-size: 14px;
}
.incorrect-chatpass {
    font-size: 14px;
    color: red;
}

.correct-chatpass {
    font-size: 14px;
    color: green;
}
#usernameExist {
    display: none;
}
#whitespaceFound {
    display: none;
}
#twoDots {
    display: none;
}
#otherThanUnderscore {
    display: none;
}
#capitalUsername {
    display: none;
}

.signup-box {
    border: thin solid rgb(206, 206, 206);
    width: 9cm;
    padding: .5cm 0cm;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: .3cm;
}

.signup-logo {
    width: 1.6cm;
    pointer-events: none;
}

.input-rest-forms {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: .3cm;
}

.signup-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: .2cm .4cm;
    row-gap: .3cm;
}

.signup-input {
    width: 6.5cm;
    font-size: 14px;
    padding: .2cm;
    border-radius: 3px;
    border: thin solid rgb(181, 181, 181);
}

.signup-submit {
    width: 7cm;
    font-size: 14px;
    padding: .2cm;
    color: white;
    border-radius: 3px;

    background-color: rgb(78, 122, 255);
    border: none;
    cursor: pointer;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    }
}