.username-block {
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;

    position: fixed;
    z-index: 1;
    backdrop-filter: blur(8px);

}

#next-button {
    margin-top: .5cm;
    display: none;
}

#usernameExist {
    color: red;
    font-size: 13px;
    margin-top: .15cm;
    display: none;
}

.close-box {
    width: .4cm;
    height: .4cm;
    cursor: pointer;
}

#usernameBlock {
    display: none;
}



.username {
    width: fit-content;
    height: fit-content;
    margin-top: 1cm;
    padding: .5cm;
    background-color: rgb(255, 255, 240);
    border: 1px solid black;
}

.username-input {
    width: 100%;
}